h1, h2, h3, h4, h5 {
  margin-block-start: 0;
  margin-block-end: 1.5rem;
}

p {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 1.5rem;
  margin-inline-end: 1.5rem;
}

ul {
  list-style-type: none;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 0;
}

address {
  display: block;
  font-style: normal;
}

/*4_elements */
h1, h2, h3 {
  font-family: "Azoft Sans Bold", "courier", "sans-serif";
}

body {
  font-size: 15px;
  font-family: "Exo 2 Regular", "georgia", "serif";
}

hgroup {
  margin: 3rem 0;
}

a {
  color: #ffcc33;
}

section ul {
  margin-bottom: 1.5rem;
}

* {
  box-sizing: border-box;
}

body {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #330033;
  margin: 0;
  padding: 0;
  color: #cebebe;
  line-height: 1.5rem;
  border-color: #336666;
  font-size: 16px;
}

main {
  display: grid;
  grid-template-columns: minmax(0, 1fr);
}

section:nth-child(even) {
  background-color: #336666;
}

nav,
footer {
  color: #330033;
  display: flex;
  background-color: #cebebe;
  border-color: #336666;
  height: 100px;
}

nav {
  top: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(180deg, #cebebe 0%, #bebebe 99%);
  display: flex;
  min-height: 8rem;
  flex-flow: column;
}

header {
  position: relative;
  top: -1rem;
  margin-bottom: -2rem;
  background-image: url("/assets/images/brickwall.png");
  background-repeat: no-repeat;
  background-position: top;
  background-attachment: fixed;
  padding: 4rem 0;
}

header,
section {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

section {
  display: flex;
  flex-flow: column;
  padding: 3rem 0;
}

section img {
  background-image: linear-gradient(to bottom, #477061, #ffcc33 50%);
  background-attachment: fixed;
  background-size: 100vw 100vh;
}

footer {
  background-image: linear-gradient(0deg, #cebebe 0%, #bebebe 99%);
  min-height: 100px;
  width: 100%;
  padding-top: 1.5rem;
  padding-left: 1.5rem;
}

@media only screen and (max-width: 767px) {
  hgroup {
    text-align: center;
  }

  .project-links a:first-child:after {
    content: "||";
    margin: 0 1rem;
  }

  #nav-menu a {
    text-decoration: none;
    color: #330033;
    font-family: "Azoft Sans Bold", "courier", "sans-serif";
  }
}
@media only screen and (min-width: 768px) {
  main {
    grid-template-columns: 1rem 1fr 1rem;
  }

  header {
    grid-column: span 3;
  }

  section {
    grid-column: 2/span 1;
  }

  body {
    font-size: 18px;
  }

  nav {
    flex-grow: 1;
  }

  #logo {
    width: 8rem;
    margin: 1rem 0 1.5rem 1rem;
  }

  #nav-menu {
    justify-content: flex-end;
    align-items: flex-end;
  }

  #nav-menu li {
    margin-left: 2rem;
  }

  #section-about {
    display: grid;
    grid-template-columns: minmax(0, 1fr) 3fr 2fr minmax(0, 1fr);
    grid-template-rows: 3rem 1fr;
    grid-template-areas: ". heading heading ." ". text image .";
  }

  #section-about h2 {
    grid-area: heading;
    margin-left: 1.5rem;
  }

  #section-about img {
    grid-area: image;
    width: 100%;
  }

  .about-text {
    grid-area: text;
  }

  #projects {
    display: flex;
    align-items: center;
  }

  .project-screenshot {
    margin-left: 3rem;
  }

  .project-image {
    max-width: 400px;
  }

  .project-data {
    margin-right: 3rem;
  }

  .project-links {
    background-color: #ffcc33;
    padding-bottom: 0;
    margin: 0 1rem;
  }

  .project-links a {
    background-color: #336666;
    padding: 1em;
    margin: 0 0.5rem;
  }

  p {
    max-width: 20rem;
  }
}
@media only screen and (min-width: 1024px) {
  main {
    display: grid;
    grid-template-columns: 1fr repeat(12, 1fr) 1fr;
  }

  header {
    grid-row: 1/2;
    grid-column: 1/9;
  }

  section#section-motto {
    grid-row: 2/3;
    grid-column: 3/15;
    padding: 4rem 0;
  }

  section#section-about {
    grid-row: 3/4;
    grid-column: 1/14;
  }

  section#section-skills {
    grid-row: 4/5;
    grid-column: 1/13;
    z-index: -99;
    padding: 4em 0;
  }

  section#section-samples {
    grid-row: 5/6;
    grid-column: 1/15;
  }

  section#section-contact {
    grid-row: 6/7;
    grid-column: 1/15;
  }

  body {
    font-size: 21px;
  }

  nav {
    flex-flow: row;
    align-items: flex-end;
  }

  #logo {
    margin: 0 3rem;
    margin: 0 0 4rem 1rem;
  }

  #nav-menu {
    margin-bottom: 3.75rem;
  }

  #nav-menu li {
    margin-left: 3rem;
  }

  #section-about {
    grid-template-columns: 1fr repeat(2, 2fr) 1fr;
    grid-template-areas: ". heading heading" ". text image";
  }

  .project-image {
    max-width: 600px;
  }
}
/*5_sections */
#section-about {
  position: relative;
  top: -1rem;
  margin-bottom: -2rem;
  background-image: url("/assets/images/woods.png");
  background-repeat: no-repeat;
  background-position: top;
  background-attachment: fixed;
}

#section-about img {
  max-width: 320px;
}

.about-text {
  max-width: 450px;
}

.contact-label {
  display: flex;
  justify-content: space-between;
}

.contact-label a:before {
  margin-left: 1em;
}

.project-screenshot {
  display: flex;
  align-items: center;
  flex-flow: column;
}

.project-image {
  max-width: 320px;
}

.project-data {
  margin: 0 1rem;
}

#projects h3,
#projects h4 {
  margin-inline-start: 1.5rem;
  margin-inline-end: 1.5rem;
}

.project-links {
  display: flex;
  justify-content: center;
}

.project-links a:hover {
  background-color: #4d9999;
}

.project-links a:active {
  background-color: #091111;
}

.project-links a:visited {
  background-color: #1a3333;
}

/*6_components */
.hero-copy {
  background-color: #336666;
  z-index: -99;
}

#logo {
  width: 5.5rem;
  margin: 1rem 0 1.5rem 1rem;
}

#logo img {
  width: 8rem;
}

p.p--break {
  margin-bottom: 1.5rem;
}

section img {
  margin-bottom: 3rem;
}

#nav-menu {
  display: flex;
  justify-content: space-around;
}

#nav-menu:last-child {
  margin-right: 2rem;
}

#nav-menu a {
  text-decoration: none;
  color: #330033;
  font-family: "Azoft Sans Bold", "courier", "sans-serif";
}